import React from 'react';
import HyperModal from 'react-hyper-modal';
// import HyperModal from '../../../../modal/hyper-modal/dist';
import Gist from 'components/Gist';
import BaseLayout from 'components/BaseLayout';
import Button from 'components/Button';
import HeaderText from 'components/HeaderText';
import './style.scss';

class HyperModalComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
  }

  openModal = () => this.setState({ isModalOpen: true });
  closeModal = () => this.setState({ isModalOpen: false });

  render() {
    return (
      <BaseLayout
        className="example-hyper-modal-wrapper"
        title="react hyper modal"
      >
        <div className="content">
          <HyperModal
            isOpen={this.state.isModalOpen}
            requestClose={this.closeModal}
            classes={{
              contentClassName: 'modal-content',
            }}
          />
          <HeaderText title="Simple controlled modal" />
          <Gist
            id="965ebd30f423f14ac342ba6be07fe54b"
            file="react-hyper-modal-simple-example.jsx"
          />
          <Button
            onClick={this.openModal}
            title="open simple modal"
            position="center"
            wide
          />

          <HeaderText title="Uncontrolled modal" />
          <Gist
            id="965ebd30f423f14ac342ba6be07fe54b"
            file="react-hyper-modal-uncontrolled-example.jsx"
          />
          <HyperModal
            classes={{
              contentClassName: 'modal-content',
            }}
            renderOpenButton={(requestOpen) => {
              return (
                <Button
                  onClick={requestOpen}
                  title="open uncontrolled modal"
                  position="center"
                  wide
                />
              )
            }}
          />

        <HeaderText title="Custom modal content" />
          <Gist
            id="965ebd30f423f14ac342ba6be07fe54b"
            file="react-hyper-modal-custom-content-example.jsx"
          />
          <HyperModal
            classes={{
              contentClassName: 'modal-content',
            }}
            renderOpenButton={(requestOpen) => {
              return (
                <Button
                  onClick={requestOpen}
                  title="open modal"
                  position="center"
                  wide
                />
              )
            }}
          >
            <div>
              Your awesome modal content
            </div>
          </HyperModal>

          <HeaderText title="Fullscreen modal" />
          <Gist
            id="965ebd30f423f14ac342ba6be07fe54b"
            file="react-hyper-modal-fullscreen-example.jsx"
          />
          <HyperModal
            classes={{
              contentClassName: 'modal-fullscreen',
            }}
            isFullscreen
            renderOpenButton={(requestOpen) => {
              return (
                <Button
                  onClick={requestOpen}
                  title="open fullscreen modal"
                  position="center"
                  wide
                />
              )
            }}
          />
        </div>
      </BaseLayout>
    );
  }
}

export default HyperModalComponent;
